.status-messages {
  padding: 10px 0;
}

.status-messages--status {
  background: #c8e6c9;
}

.status-messages--warning {
  background: #ffecb3;
}

.status-messages--error {
  background: #ffcdd2;
}

.status-messages__list {
  padding: 0;
  list-style: none;
}

.status-messages__message {
  margin-top: 10px;
}

.status-messages__message:first-child {
  margin-top: 0;
}

.status-messages__message:last-child {
  margin-bottom: 0;
}

